.info {
  border-top: 1px solid $line;
  margin-top: 43px;

  @include ptmono(13px, 400, 24px);
  color: $nav;

  @media (max-width: $tablet-width) {
    margin-top: 22px;
    margin-bottom: 22px;
    border-top: 1px solid $line;
  }

  @media (max-width: $mobile-width) {
    border-top: 0;
  }
}

.info__list {
  display: flex;
  justify-content: space-start;
  // max-width: 1411px;
  max-width: 1461px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 23px;

  @include ptmono(13px, 400, 24px);
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-top: 14px;

  @media (max-width: $tablet-width) {
    padding: 0 25px;
    margin-top: 22px;
  }

  @media (max-width: $mobile-width) {
    display: flex;
    width: auto;
    flex-direction: column;
    align-items: center;
    margin-top: 28px;
  }
}

.info__years {
  margin-left: 5%;

  @media (max-width: $tablet-width) {
    position: absolute;
    width: 238px;
    top: 37px;
    right: 0;
    left: 0;
    margin-left: 42.5%;
    text-transform: uppercase;
  }

  @media (max-width: $mobile-width) {
    top: 125px;
    margin-left: auto;
    margin-right: auto;
  }
}

.info__privacy-policy {
  margin-left: 9.8%;

  @media (max-width: $tablet-width) {
    margin-left: 0;
  }

  @media (max-width: $mobile-width) {
    text-align: center;
  }
}

.info__copyright {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.info__developer {
  margin-left: auto;

  @media (max-width: $mobile-width) {
    margin-left: 0;
    margin-top: 20px;
  }
}
