.brand {
  display: flex;
  align-items: center;
}

.brand__logo {
  width: 52px;
  height: 52px;
  margin-right: 13px;
}

.brand__name {
  min-width: 135px;
  margin-top: 5px;
  margin-bottom: 3px;
  color: $white;

  @include roboto-cond(25px, 700, 29px);

  @media (max-width: $tablet-width) {
    min-width: 137px;
    width: auto;
    margin-right: 0;
    padding-left: 3px;
  }

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.brand__slogan {
  display: block;
  color: $brand-slogan;

  @include roboto();
}
